label {
  font-size: 14px;
  color: $label;
  font-weight: 500;
  margin: 0 0 10px;
  display: block;
}
.form-control {
  margin: 0 0 20px;
  position: relative;

  input[type="text"], input[type="password"], input[type="search"], input[type="email"], input[type="number"] {
    @include form-field-background();
  }

  &.phone-number {
    input {
      padding-left: 80px;
    }

    .is-phone {
      background: $light-grey;
      border-right: 2px solid #ddd;
      color: $text-light-grey;
      font-size: 16px;
      display: inline-flex;
      align-items: center;
      padding: 12px;
      border-radius: 4px 0 0 4px;
      position: absolute;
      left: 0;

      i {
        width: 16px;
        height: 16px;
        display: inline-block;
        background: url("../images/chevron-grey.svg") no-repeat;
        position: relative;
        top: 0;
        transform: rotate(90deg);
        left: 5px;
      }
    }
  }
}
.btn {
  width: 100%;
  padding: 14px 18px;
  border: 0;
  border-radius: 4px;
  font-size: 16px;
  color: $white;
  font-weight: 700;
  cursor: pointer;
  text-decoration: none !important;

  &.btn-primary {
    background: $primary-button;
  }

  &.btn-disabled {
    background: $disable-button;
    cursor: default;
  }

  &.btn-loader {
    background: $loader-button;

    i {
      width: 16px;
      height: 16px;
      border: 2px solid #FFF;
      border-bottom-color: transparent;
      border-radius: 50%;
      display: inline-block;
      box-sizing: border-box;
      animation: rotation 1s linear infinite;
      background: transparent;
    }
  }
}
label.radio-box {
	display: flex;
	align-items: center;
	margin: 10px 20px 0 0;
	cursor: pointer;
  position: relative;
  left: -10px;

  span {
    text-transform: capitalize;
  }

  input[type="radio"] {
    opacity: 0;
    position: relative;
    left: 10px;

    &:checked~.radio-layer {
      outline-color: $link-color;
      background: $link-color;
    }
    
    &:checked~.value {
      color: $link-color;
    }
  }

  .radio-layer {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    outline-offset: 3px;
    outline: 3px solid #ddd;
    margin-right: 15px;
    transition: all 500ms;
  }
}
.or-block {
  text-align: center;
  color: $text-grey;
  position: relative;

  &::before, &::after {
    content: "";
    width: 25%;
    height: 1px;
    background: $text-light-grey;
    position: absolute;
    top: 8px;
    left: 10%;
  }

  &::after {
    left: auto;
    right: 10%;
  }
}
.error-message {
  border: 1px solid #F8A5AF;
  background: #FCE8EB;
  padding: 8px;
  border-radius: 4px;
  color: #AC162C;
  font-size: 12px;
  font-weight: bold;
  position: absolute;
  width: 100%;
  top: 60px;
  display: none;
}
.error-show {
  display: block;
}
.dropdown-execute {
  @include form-field-background();
  position: relative;
  cursor: pointer;
  margin-bottom: 20px;

  &.arrow-up::after, &.arrow-down::after {
    content: "";
    width: 16px;
    height: 16px;
    background: url('../images/chevron-grey.svg') no-repeat;
    position: absolute;
    top: 10px;
    right: 10px;    
    transform: rotate(-90deg);
    transition: all 500ms;
  }

  &.arrow-down::after {
    transform: rotate(90deg);
  }
}
.dropdown {
  background: $light-grey;
  border-radius: 4px;
  padding: 5px;
  position: absolute;
  top: 75px;
  width: 100%;
  z-index: 999;
  display: none;

  &.show {
    display: block;
  }

  li {
    padding: 10px 12px;
    border-radius: 4px;
    margin: 0 0 10px;
    color: $text-grey;
    font-size: 14px;
    transition: all 1000ms;
    cursor: pointer;

    &:last-child {
      margin: 0;
    }

    &:hover {
      background: $lighter-grey;
      color: $white;
    }
  }
}
@keyframes rotation {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
} 