@import '../../assets/scss/variable';
.footer {
    background: $neutral;
    width: 100%;
    padding: 10px 0 5px;

    ul li, a {
        color: $white;
        font-size: 14px;
        font-weight: 400;
    }

    ul li {
        position: relative;
        margin: 0 50px 0 0;

        &::after {
            content: "";
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background: #fff;
            position: absolute;
            right: -30px;
            top: 7px;
        }

        &:last-child::after {
            content: none;
        }
    }

    .affinity-venture {
        color: $white;
        font-size: 14px;

        img {
            width: 100px;
            margin: 0 15px;
        }
    }
}