@import '../../assets/scss/variable';
.auth-container {
    padding: 25px 25px 25px 100px;
    position: relative;

    .auth-components {
        form {
            width: 400px;
        }

        .logo {
            margin-bottom: 50px;
        }

        .auth-login {
            position: relative;

            h1 {
                color: $title;
                font-size: 28px;
                margin: 0 0 70px;
            }

            p {
                font-size: 14px;
                font-weight: 500;
                color: $neutral;
            }

            .verify-otp-fields {
                .verify-otp-field {
                    flex-basis: 25%;    
                    margin: 0 30px 0 0; 
                    position: relative;   
                    
                    &:last-child {
                        margin: 0;
                    }

                    &::after {
                        content: "";
                        width: 100%;
                        height: 3px;
                        background: #ddd;
                        border-radius: 4px;
                        position: absolute;
                        bottom: -10px;
                        left: 0;
                    }
                }
                
                input {
                    font-size: 36px;
                    color: $neutral;
                    text-align: center;  
                    width: 100%;          
                    border: 0;
                    font-weight: 700;
                    outline: none;
                }

            }

            .resend-text {
                font-size: 12px;
                color: $neutral;
                display: none;

                &.resend-code {
                    display: block;
                }
            }
        }

    }

    .dont-have-account {
        margin: 50px 0 0;
        padding: 16px 0;
        color: $label;
        border: 1.5px solid $text-light-grey;
        border-radius: 10px;
        
        label {
            font-size: 16px;
            display: block;
        }
    }

    .auth-form-scroll {
        width: 400px;
        padding: 0 0 0 2px;
    }    
}

.choose-user-type {    
    height: 100vh;
    width: 600px;
    /* margin: 0 auto; */
    position: fixed;
    width: 100%;
    background: #fff;
    z-index: 9999;
    left: 0;
    top: 0;    

    h1 {
        font-size: 28px;
        color: $title;
        text-align: center; 
        margin: 0 0 40px;   
        width: 600px;        
    }

    .choose-user {
        padding: 20px;
        border-radius: 8px;
        border: 1px solid $stroke;
        width: 280px;
        margin: 0 30px 0 0;
        cursor: pointer;
        transition: all 1000ms;

        &.active {
            border: 1px solid $blue-50;
            background: $blue-05;
            box-shadow: 0px 0px 4px 0px rgba(66, 175, 255, 0.50), 0px 0px 4px 0px rgba(66, 175, 255, 0.50);
        }

        .choose-user-icon {
            width: 220px;
            height: 220px;
            margin: 0 auto 10px;
            background: #F5F7FA url('../../assets/images/account-icon.svg') no-repeat center;
            border-radius: 50%;
        }

        h3 {
            font-size: 24px;
            margin: 0 0 20px;
        }

        a {
            display: block;
            margin-top: 30px;
        }
    }
}