@import '../../../assets/scss/variable';
.auth-app-preview {
	// flex-basis: 50%;
	height: 93vh;
	// background: url(../../assets/images/auth/auth-app-preview-bg.svg) no-repeat;
	// background-size: 100% 100%; 
    background: linear-gradient(180deg, #E54F65 0%, #BB3E64 18.06%, #752349 52.5%, #54163D 69.12%, #3E0E25 83.99%);
    border-radius: 40px;
    padding: 0 5%;
    position: relative;
    overflow: hidden;

    @mixin app-preview-circle() {
        content: "";
        background-size: 100% 100% !important;
        border-radius: 50%;
        position: absolute;
        z-index: 1;
    }

    &::before, &::after {
        @include app-preview-circle();
        width: 227px;
        height: 227px;
        background: url('../../../assets/images/auth/app-preview-left-bottom-circle.svg') no-repeat;
        left: -60px;
        bottom: 20px;
    }

    &::after {
        background: url('../../../assets/images/auth/app-preview-right-top-circle.svg') no-repeat;
        width: 130px;
        height: 130px;
        top: 0;
        right: -40px;
        left: auto;
    }

    .app-preview {
        display: flex;
        justify-content: center;
        margin: 30% 0 0;

        img {
            width: 65%;
            position: relative;
            z-index: 5;
        }

        &::before, &::after {
            @include app-preview-circle();
            width: 144px;
            height: 144px;
            background: url('../../../assets/images/auth/app-preview-left-top-circle.svg') no-repeat;
            left: 20px;
            top: 30px;
        }
    }

    .download-veve-market-place {
        color: $white;
        margin: 10px 0 0;
        font-size: 12px;
        font-weight: 500;
        position: relative;
        z-index: 5;
    }
}