// Color system
$white: #fff;
$black: #000;
$title: #111;
$label: #1A1919;
$neutral: #1A1919;
$text-grey: #666;
$text-light-grey: #999;
$light-grey: #f9f9f9;
$lighter-grey: #aaa;
$link-color: #0C99FF;
$border-color: #999;
$primary-button: #DE1D39;
$disable-button: $text-light-grey;
$loader-button: #F8A5AF;
$stroke: #ddd;
$blue-50: #42AFFF;
$blue-05: #F5F7FA;
$surface-blue: #E2F5FF;

@mixin form-field-background {
    background: $light-grey;
    border-radius: 4px;
    border: 0;
    padding: 12px;
    color: $text-light-grey;
    font-size: 16px;
    width: 100%;
}