@import '../../../assets/scss/theme.scss';
.quick-start-wrapper {
    background: linear-gradient(180deg, rgba(229,79,101,0.75) 0%, rgba(187,62,100,0.75) 21.50%, rgba(117,35,73,0.75) 62.50%, rgba(84,22,61,0.75) 82.29%, rgba(62,14,37,0.75) 100%);
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    .quick-start {
        background: $white;
        border-radius: 16px;
        padding: 50px 30px 20px;
        width: 480px;

        .quick-start-items {
            height: 430px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            .quick-start-item p {
                font-size: 16px;
                color: $text-grey;
                margin: 30px 0 0;

                b {
                    color: $label;
                }
            }
        }
    }
}

// slider code
.slider {
    position: relative;

    .slider-items-container {
        width: 420px;
        overflow: hidden;

        .slider-items {
            display: flex;
            transition: all 800ms;
            position: relative;

            .quick-start-item {
                width: 420px;
            }
        }
    }

    .slider-counters {
        display: flex;
        justify-content: center;
        margin: 30px 0 0;

        .slider-counter {
            background: $surface-blue;
            width: 40px;
            height: 4px;
            border-radius: 2px;
            margin: 0 4px 0 0;
            transition: all 1000ms;

            &:last-child {
                margin: 0;
            }

            &.active {
                background: #0C99FF;
            }
        }
    }

    .slider-action {
        &.hide {
            display: none;
        }
        &.show {
            display: block;
        }
    }

    .slider-next, .slider-previous {
        width: 28px;
        height: 28px;
        background: url('../../../assets/images/chevron-with-bg.svg') no-repeat;
        background-size: 100%;
        position: absolute;
        top: 50%;
        right: -43px;
        cursor: pointer;
    }

    .slider-previous {
        left: -43px;
        right: auto;
        transform: rotate(180deg);
    }
}